body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: rgba(7, 27, 81, 0.9);
}

.loader {
  background: #4b4949;
  height: 100vh;
  width: 100vw;
  position: absolute;
}

.loader .circle {
  width: 60px;
  height: 60px;
  border: 10px solid white;
  border-radius: 50%;
  border-top-color: lightcoral;
  /* border-bottom-color: lightcoral; */
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-animation: circle-rot 2s linear infinite;
          animation: circle-rot 2s linear infinite;
}

@-webkit-keyframes circle-rot {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes circle-rot {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

