.loader {
  background: #4b4949;
  height: 100vh;
  width: 100vw;
  position: absolute;
}

.loader .circle {
  width: 60px;
  height: 60px;
  border: 10px solid white;
  border-radius: 50%;
  border-top-color: lightcoral;
  /* border-bottom-color: lightcoral; */
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  animation: circle-rot 2s linear infinite;
}

@keyframes circle-rot {
  100% {
    transform: rotate(360deg);
  }
}
